"use client";

import InfoCard from "@/components/Left/Info/InfoCard";
import SendContent from "@/components/Left/SendContent/SendContent";
import Topic from "@/components/Left/Topic/Topic";
import PostCard from "@/components/PostCard/PostCard";
import { H0 } from "@/components/Typography/Heading.styles";
import Image from "next/image";
import IgImage from "@/assets/image/ig_pic.png";
import { useEffect, useState } from "react";

export interface Post {
  title: string;
  publishedAt: string;
  mainContent: string;
  cover: Cover;
}

export interface Cover {
  url: string;
}

export default function Home() {
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(false);

  const handleGetPost = async () => {
    setLoading(true);
    try {
      const response = await fetch(
        "http://localhost:1337/api/blogs?populate=*"
      );
      const data = await response.json();

      if (!response.ok) {
        setLoading(false);
        return;
      }

      console.log(data.data);
      setPosts(data.data);
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    handleGetPost();
  }, []);
  return (
    <div className="font-sans flex flex-row items-start justify-items-start min-h-screen p-8 pb-20 gap-16 sm:p-20"></div>
  );
}
